var sliderWidth = 96; // 需要设置slider的宽度，用于计算中间位置
var app = getApp();
import common from '../../../publicjs/common.js'
export default {
	data() {
		return {
			tabs: [
				{
					icon: 'icon-zixun',
					title: '精选资讯'
				},
				{
					icon: 'icon-pingtaiguanli',
					title: '平台介绍'
				}
			],
			activeIndex: 0, //0：精选资讯， 1：平台介绍
			sliderLeft: 0,
			sliderOffset: 0,
			activeArray: []
		};
	},
	onLoad:function(){
		//获取字体信息
		common.fonts();
		this.getlist(); //获取页面数据
		uni.startPullDownRefresh();  //刷新
	},
	//停止刷新
	onPullDownRefresh() {
		setTimeout(function () {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	onShow: function() {
		let this_ = this;
		uni.getSystemInfo({
			success: function(res) {
				this_.sliderLeft = (res.windowWidth / this_.tabs.length - sliderWidth) / 2;
				this_.sliderOffset = (res.windowWidth / this_.tabs.length) * this_.activeIndex;
			}
		});
	},
	computed: {
		
	},
	watch: {},
	methods: {
		tabClick: function(e) {
			this.sliderOffset = e.currentTarget.offsetLeft;
			this.activeIndex = e.currentTarget.id;
			this.getlist();
		},
		//获取页面数据
		getlist() {
			let url = "/graphic/selection";
			if(this.activeIndex == 1) {
				url = "/graphic/intro";
			}
			common.requestURL({
				url: url,
				method: 'GET',
			}).then( res => {
				this.activeArray = res.data;
			})
		},
		
	}
};
